import { useCallback, useMemo, useState } from 'react'
import PropTypes from 'prop-types'

import { Control, Label, Select } from '../../Controls'
import ActionButton from '../../../Common/ActionButton'
import DialogBox from '../../../Common/DialogBox'
import Modal from '../../../Common/Modal'
import github from '../api'
import Spinner from '../../../Common/Spinner'

function CreateBranch ({ onConfirm, onCancel, branches, selected, repo }) {
  const branchOptions = useMemo(() => branches.map(({ name }) => ({ id: name, name })), [branches])
  const [state, setState] = useState({
    name: '',
    source: branchOptions.find(branch => branch.id === selected),
    submitting: false
  })

  const ready = useMemo(() => (
    state.name &&
    state.source &&
    !state.submitting
  ), [state])

  const handleChangeName = useCallback(event => {
    setState(state => ({ ...state, name: event.target.value }))
  }, [setState])

  const handleChangeSource = useCallback(branch => {
    setState(state => ({ ...state, source: branch }))
  }, [setState])

  const handleSubmit = useCallback(async () => {
    setState(state => ({ ...state, submitting: true }))

    try {
      await github.createBranch(repo, state.source.id, state.name)
    } catch (err) {
      console.error('Branch creation failed', err)
      setState(state => ({ ...state, submitting: false }))
      return
    }

    setState(state => ({ ...state, submitting: false }))
    onConfirm(state.name)
  }, [repo, state.source, state.name, setState, onConfirm])

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox dismissText={null}>
        <h2>Create new branch</h2>

        <Control>
          <Label>
            Branch <input
              type="text"
              value={state.name}
              disabled={state.submitting}
              onChange={handleChangeName}
            />
          </Label>
        </Control>

        <Label>
          Source <Select
            value={state.source}
            options={branchOptions}
            getOptionValue={option => option.id}
            getOptionLabel={option => option.name}
            onChange={handleChangeSource}
          />
        </Label>

        <p style={{ textAlign: 'center', gap: '10px' }}>
          <ActionButton disabled={!ready} onClick={handleSubmit}>
            {state.submitting ? <Spinner /> : <>Confirm</>}
          </ActionButton>
          <ActionButton disabled={state.submitting} onClick={onCancel} secondary>Cancel</ActionButton>
        </p>
      </DialogBox>
    </Modal>
  )
}

CreateBranch.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  branches: PropTypes.array.isRequired,
  selected: PropTypes.string.isRequired,
  repo: PropTypes.any
}

export default CreateBranch
