import isEqual from 'lodash/isEqual'
import { useCallback, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import BuildLink from './BuildLink'
import styles from './styles.module.css'
import ActionButton from '../../Common/ActionButton'
import Commit from './Dialogs/Commit'
import Icon from '../../Common/Icon'
import Spinner from '../../Common/Spinner'
import github from './api'
import ActionMenu from '../../Common/ActionMenu'
import CreateBranch from './Dialogs/CreateBranch'
import { Control } from '../Controls'

const stateSelector = state => state

export function Actions ({ state: pickerState, actions: pickerActions }) {
  const dispatch = useDispatch()
  const state = useSelector(stateSelector)
  const {
    source,
    sourceOther,
    keyboard,
    _unmodifiedKeymap,
    saving,
    experimentalSettings,
    latestBuild
  } = state
  const [showBranchModal, setShowBranchModal] = useState(false)

  const { loadedGitInfo } = pickerState

  const moreActions = [
    ...github.installations.map(installation => ({
      callback: () => github.manageInstallationRepos(installation.id),
      content: <>Manage repos for <em>{installation.account.login}</em></>
    })),
    {
      callback: () => setShowBranchModal(true),
      content: <>Create new branch</>
    },
    {
      callback: () => github.logout(),
      content: <>Log out</>
    }
  ]

  const hasChanged = keyboard && !isEqual(keyboard.keymap, _unmodifiedKeymap)

  function openCommitDialog () {
    dispatch({ type: 'COMMIT_DIALOG_OPEN' })
  }

  const handleCommitChanges = useCallback(function (message) {
    const { repository, branch } = sourceOther.github

    ;(async function () {
      dispatch({ type: 'COMMIT_DIALOG_CLOSE' })
      dispatch({ type: 'KEYMAP_SAVE_START' })

      const { layout, keymap } = keyboard

      try {
        const { data: updated } = await github.commitChanges(repository, branch, layout, keymap, {
          message,
          experimentalSettings,
          keyboard: sourceOther.github.keyboard,
          loadedGitInfo
        })
        dispatch({ type: 'KEYMAP_SAVE_SUCCESS', keymap: updated })
      } catch (err) {
        const details = err.response?.data?.commitMismatch
          ? { commitMismatch: true }
          : {}

        dispatch({ type: 'KEYMAP_SAVE_FAILURE', details })
      }
    })()
  }, [sourceOther, loadedGitInfo, keyboard, dispatch, experimentalSettings])

  return (
    <div className={styles.pickerActions}>
      {source === 'github' && !sourceOther.github.isProtected && (
        <ActionButton
          title="Commit keymap changes to GitHub repository"
          disabled={!hasChanged}
          onClick={openCommitDialog}
        >
          {saving ? <>Saving <Spinner /></> : 'Save changes'}
        </ActionButton>
      )}
      {source === 'github' && sourceOther.github.isProtected && (
        <ActionButton title="Cannot commit changes to this branch" disabled>
          <Icon name="shield" /> Read-only
        </ActionButton>
      )}
      {state.commitDialogOpen && (
        <Commit
          onConfirm={handleCommitChanges}
          onCancel={() => dispatch({ type: 'COMMIT_DIALOG_CLOSE' })}
        />
      )}

      {latestBuild && <BuildLink latestRun={latestBuild} />}

      <Control>
        <ActionMenu actions={moreActions} />
      </Control>

      {showBranchModal && (
        <CreateBranch
          repo={pickerState.selectedRepoId}
          branches={pickerState.branches}
          selected={pickerState.selectedBranchName}
          onCancel={() => setShowBranchModal(false)}
          onConfirm={branch => {
            setShowBranchModal(false)
            pickerActions.createdBranch({ name: branch })
            pickerActions.selectedBranch({ name: branch })
          }}
        />
      )}
    </div>
  )
}
